      that.isCheck = true;
      that.checkOrgTreeId = val.cur_obj.orgid;
      that.checkOrgTreeType = val.cur_obj.type;
      that.currentSelTreeNodeIds = val.id;
      console.log('that.currentSelTreeNodeIds:' + that.currentSelTreeNodeIds)
      console.log('that.checkOrgTreeId:' + that.checkOrgTreeId)
      console.log('that.checkOrgTreeType:' + that.checkOrgTreeType)


      // 每次赋值前都需要清空
      that.userOrgIds = {};
      that.thermalUserForm.cui_company_id = '';
      that.thermalUserForm.cui_child_company_id = '';
      that.thermalUserForm.cui_institute_id = '';
      that.thermalUserForm.cui_station_id = '';
      for (let i = 0; i < val.obj.length; i++) {
        switch (val.obj[i].type) {
          case '1':
            that.thermalUserForm.cui_company_id = val.obj[i].id;
            break;
          case '2':
            that.thermalUserForm.cui_child_company_id = val.obj[i].id;
            break;
          case '3':
            that.thermalUserForm.cui_institute_id = val.obj[i].id;
            break;
          case '4':
            that.thermalUserForm.cui_station_id = val.obj[i].id;
            break;
        }
      }

      for (let i1 = 0; i1 < val.obj.length; i1++) {
        that.userOrgIds[val.obj[i1].id] = true;
        // console.info("===机构ID：============"+val.obj[i1].id);
      }


      that.tmpCaption = [];
      for (var i1 = 0; i1 < val.caption.length; i1++) {
        that.tmpCaption[i1] = that.customGlobal.decrypt(val.caption[i1], that.customGlobal.cryptoKey);
      }

      that.queryValveCurrentData();

    });
    // this.getQueryDetail();

  },
  created() {
    this.initAccOrgTreeData(this.customGlobal.userOrgan, this.customGlobal.permissionKeyOrganKey.orgkey);
    let userInfo = this.customGlobal.userInfoExt;
    this.loginName = userInfo.cui_login_id;
    if (userInfo.cui_admin_type === '0' || userInfo.cui_type === '0') {
      this.adminStatus = false;
      this.submitColm = true;
    }

    if (userInfo.cui_type === '0') {
      this.checkOrgTreeType = userInfo.cui_org_type;
    } else {
      this.checkOrgTreeType = userInfo.cui_type;
    }

    this.queryOrgType = this.checkOrgTreeType;

    this.queryValveCurrentData(1);

    let data = JSON.parse(JSON.stringify(this.customGlobal.userMenu));
    // 菜单权限树初始化
    this.initAccountTableTreeData(data);
    // 按钮权限树初始化
    this.initAccountBtnTableTreeData(data);
  },
  directives: {   //自定义函数书写位置
    trigger: {
      inserted(el, binging) {
        //  console.log("inserted:"+el.id);
        if (el.id === 'ballBtn') {
          el.click();
        }

      }
    },
    drag(el) {
      let oDiv = el;  // 当前元素
      let firstTime = "", lastTime = "";  //声明时间用来区分拖拽和单机
      document.onselectstart = function () {       // 禁止选择网页上的文字
        return false;
      }
      oDiv.onmousedown = function (e) {
        let disX = e.clientX - oDiv.offsetLeft          // 鼠标按下，计算当前元素距离可视区的距离    // 算出鼠标相对元素的位置     // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let disY = e.clientY - oDiv.offsetTop
        document.getElementById("dragbtn").setAttribute("data-flag", false);
        document.getElementById("dragbtn").setAttribute("show2", oDiv.offsetLeft);
        firstTime = new Date().getTime();
        document.onmousemove = function (e) {
          let l = e.clientX - disX               // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置 左侧多少 ，不是定型尺寸哦 ，是定位尺寸
          let t = e.clientY - disY
          // console.log(e.clientX+"移动后的鼠标位置 ，已经和这个没移动前不一样了  ")
          if (l <= 0) {
            l = 0;
          } else if (l >= document.documentElement.clientWidth - oDiv.clientWidth) {
            //document.documentElement.clientWidth 屏幕的可视宽度
            l = document.documentElement.clientWidth - oDiv.clientWidth
          }
          document.getElementById("dragbtn").setAttribute("show2", l.toString()); //无法使用this，只好赋值给属性
          if (t <= 0) {
            t = 0;
          } else if (t >= document.documentElement.clientHeight - oDiv.clientHeight) {  //本身的宽高  oDiv.clientHeight
            t = document.documentElement.clientHeight - oDiv.clientHeight              // document.documentElement.clientHeight 屏幕的可视高度
          }
          oDiv.style.left = l + 'px'             // 移动当前元素
          oDiv.style.top = t + 'px'
        }
        document.onmouseup = function (e) {
          document.onmousemove = null;
          document.onmouseup = null;
          lastTime = new Date().getTime();
          if ((lastTime - firstTime) < 200) {
            document.getElementById("dragbtn").setAttribute("data-flag", true)
          }
        }
        return false          // return false不加的话可能导致黏连，就是拖到一个地方时div粘在鼠标上不下来，相当于onmouseup失效
      }
    }
  },

