    /** 查询方法 */
    queryValveCurrentData(value) {
      let queryCompany = this.companyIds;
      let queryChild = this.childCompanyIds;
      let queryInstitute = this.instituteIds;
      let queryStation = this.stationIds;

      let cui_type = this.customGlobal.userInfoExt.cui_type;//用户类型
