      // let cui_admin_type = this.customGlobal.userInfoExt.cui_admin_type;//超级管理员标示
      let type = this.checkOrgTreeType;
      let orgid = this.checkOrgTreeId;
      let currentSelTreeNodeIds = this.currentSelTreeNodeIds;
      console.log('currentSelTreeNodeIds2:' + currentSelTreeNodeIds)
      // console.log('that.obj_bank_center_id:'+this.obj_province_id)
      // console.log('that.obj_bank_center_id:'+this.obj_city_id)
      // console.log('that.obj_bank_center_id:'+this.obj_county_id)
