    /** 表单提交 */
    submitForm(formName) {
      let url_method = '';
      let msg_show = '';
      let flag = this.tableOperFlag;
      if (this.queryOrgType > this.checkOrgTreeType) {
        this.$alert('您还未获取此机构的操作权限，请联系系统管理员！', '新增出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      switch (flag) {
        case 1:
          // console.log('this.customGlobal.serverUrl.bankUser_add:'+this.customGlobal.serverUrl.bankUser_add)
          url_method = this.customGlobal.serverUrl.bankUser_add;
          msg_show = "添加";
          break;
        case 2:
          url_method = this.customGlobal.serverUrl.bankUser_update;
          msg_show = "编辑";
          break;
      }
      //将存储角色ID的map转换为数组
      let objPermissionKey = {};
      this.tableRowIdMap.forEach(function (value, key) {
        objPermissionKey[key] = true;
      });

      let cui_type = this.customGlobal.userInfoExt.cui_type;//用户类型
      let cui_org_type = this.customGlobal.userInfoExt.cui_org_type;//平台管理员组织机构数据域
      // let cui_admin_type = this.customGlobal.userInfoExt.cui_admin_type;//超级管理员标示
      let type = this.checkOrgTreeType;
      let orgid = this.checkOrgTreeId;
      let obj_bank_center_id = '';
      let obj_province_id = '';
      let obj_city_id = '';
      let obj_county_id = '';
      let obj_station_id = '';
      console.log('flag' + flag)
      if (flag === 1||this.currentSelTreeNodeIds) {
        for (let i = 0; i < this.currentSelTreeNodeIds.length; i++) {
          if (this.currentSelTreeNodeIds[i].startsWith("CEN_")) {
            obj_bank_center_id = this.currentSelTreeNodeIds[i];
          } else if (this.currentSelTreeNodeIds[i].startsWith("PRO_")) {
            obj_province_id = this.currentSelTreeNodeIds[i];
          } else if (this.currentSelTreeNodeIds[i].startsWith("CIT_")) {
            obj_city_id = this.currentSelTreeNodeIds[i];
          } else if (this.currentSelTreeNodeIds[i].startsWith("STA_")) {
            obj_station_id = this.currentSelTreeNodeIds[i];
          } else if (this.currentSelTreeNodeIds[i].startsWith("COU_")) {
            obj_county_id = this.currentSelTreeNodeIds[i];
          }
        }
      } else if (flag === 2||this.currentSelTreeNodeIds) {
        obj_bank_center_id = this.cui_bank_center_id;
        obj_city_id = this.cui_city_id;
        obj_county_id = this.cui_county_id;
        obj_province_id = this.cui_province_id;
        obj_station_id = this.cui_station_id;
        type = this.type;
      }
      console.log('obj_bank_center_id:' + obj_bank_center_id)
      console.log('obj_province_id:' + obj_province_id)
      console.log('obj_city_id:' + obj_city_id)
      console.log('obj_county_id:' + obj_county_id)
      console.log('obj_station_id:' + obj_station_id)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("结构树id:" + JSON.stringify(this.userOrgIds))
          let _jsondata_ = {
            orgid: orgid,
            type: type,
            cui_type: cui_type,
            cui_org_type: cui_org_type,
            // cui_admin_type:cui_admin_type,
            obj_bank_center_id: obj_bank_center_id,
            obj_province_id: obj_province_id,
            obj_city_id: obj_city_id,
            obj_county_id: obj_county_id,
            obj_station_id: obj_station_id,


            cui_uuid: this.thermalUserForm.cui_uuid,
            cui_login_id: this.thermalUserForm.cui_login_id,
            cui_password: this.customGlobal.md5(this.thermalUserForm.cui_password),
            cui_realname: this.thermalUserForm.cui_realname,
            cui_concat_phone: this.thermalUserForm.cui_concat_phone,
            cui_department: this.thermalUserForm.cui_department,
            cui_status: this.thermalUserForm.cui_status == true ? '1' : '2',
            check_org_id: this.checkOrgTreeId,
            check_org_type: this.checkOrgTreeType,
            cui_station_id: this.thermalUserForm.cui_station_id,
            cui_admin_type: this.thermalUserForm.cui_admin_type == true ? '0' : '1',
            org_id: this.userOrgIds, //后台创建redis信息使用
            update_org_type: this.thermalUserForm.update_org_type,//更新时查询是否存在管理员使用
            // cui_wx_nick_name:this.thermalUserForm.cui_wx_nick_name,
            // cui_wx_open_id:this.thermalUserForm.cui_wx_open_id,
            // cui_wx_union_id:this.thermalUserForm.cui_wx_union_id,
            // cui_company_id:this.thermalUserForm.cui_company_id,
            // cui_child_company_id:this.thermalUserForm.cui_child_company_id,
            // cui_institute_id:this.thermalUserForm.cui_institute_id,
            permission_id: objPermissionKey,//权限点
          };
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, .7)'
          });
          //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
          var p1 = this.customGlobal.postHttpClient(
            url_method,// 方法路径
            this.customGlobal.serverUrl.orgBaseURI,//服务器地址
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
          );
          //post提交后返回值操作
          p1.then((res) => {
            //  console.log("异步调用成功返回:"+JSON.stringify(res));
            if (res.data.status == 'success') {
              //  console.info("===机构ID：============"+JSON.stringify(this.userOrgIds));
              this.thermalUserFormVisible = false;

              //提交之后 清空表单及系统公用变量值，防止数据出错
              this.tableRowIdMap = new Map();
              this.noCheckTreeTable(this.tableTreeData);

              this.queryValveCurrentData();
              loading.close();
              this.$message({
                type: 'success',
                message: msg_show + '成功!'
              });
            } else {
              //  console.info("编辑失败");
              loading.close();
              // this.$alert('登录已超时，请重新登录！', '', {
              //   confirmButtonText: '确定',
              //   callback: action => {
              //     //  console.info('data:'+JSON.stringify(res.data));
              //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
              //       this.$router.replace({path: '/login'});
              //     }
              //   }
              // });
              if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
                this.$router.replace({path: '/login'});
              }
            }
          }, error => {
            loading.close();
            //  console.log("异步调用出错:"+JSON.stringify(error));
          }).catch((res) => {
            loading.close();
            //  console.log("异步调用失败返回:"+JSON.stringify(res));
          });

        } else {
          //  console.log('error submit!!');
          return false;
        }
      });
    },

