    /** 查询用户的菜单权限ID数组 */
    queryUserRoleIds(userLoginId) {
      let _jsondata_ = {
        username: userLoginId,
      };
      // console.info("===查询用户的菜单权限ID数组===传递的参数==="+JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryUserOrgAndPermissionKey,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        if (res.data.status == 'success') {
          // 清空已选择的菜单树 map值，下方需要重新赋值，防止数据重复
          this.tableRowIdMap = new Map();
          this.userQueryRoleIds = {};
          //处理权限ID
          let permissionKey = JSON.parse(res.data.permissionkey);
          this.userQueryRoleIds = permissionKey;
          let i = 0
          // console.log("===用户权限ID查询事件=====查询到的用户机构与权限ID============="+permissionKey);
          for (let p1 of Object.keys(permissionKey)) {
            if (p1 !== '-1') {
              this.tableRowIdMap.set(p1, true);
              i++;
            }
          }
          this.menuNum = i;
          // 用户菜单权限回显 先清空 后赋值
          this.noCheckTreeTable(this.tableTreeData)
          this.initTableDataPermissionStatus(this.userQueryRoleIds, this.tableTreeData);
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '权限查询出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
