    /** --------------------------------------权限树使用------------------------------------ */

    /** treeTable 全选方法 */
    tableTreeSelectAll() {//选择全部及不选择
      this.tableCheckedKeys = !this.tableCheckedKeys;

      this.spliteTable(this.tableTreeData, this.tableCheckedKeys);
      let table_arr = this.$refs.thermalRoleTreeTable.selection;
      // console.info("All方法选择的行："+JSON.stringify(table_arr) );

      if (table_arr.length === 0) {
        this.tableRowIdMap.clear();
      } else {
        for (let i = 0; i < table_arr.length; i++) {
          this.tableRowIdMap.set(table_arr[i].id, true);
        }
      }
      this.menuNum = this.tableRowIdMap.size;
      // console.info("选中行："+JSON.stringify(this.tableRowIdMap) );
    },

    /** 处理数据  */
    spliteTable(rows, flag) {
      for (let i = 0; i < rows.length; i++) {
        // console.info("spliteTable_i:"+i);
        this.$refs.thermalRoleTreeTable.toggleRowSelection(rows[i], flag);
        rows[i].status = flag;

        //this.tableRowIdMap.set(rows[i].id,flag===null?true:false);
        if (this.tableRowIdMap.has(rows[i].id) === false) {
          this.tableRowIdMap.set(rows[i].id, true);
        } else if (this.tableRowIdMap.get(rows[i].id) === true) {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id);
        } else {
          this.tableRowIdMap.set(rows[i].id, true, rows[i].caption);
        }

        if (rows[i].detail) {
          this.spliteTable(rows[i].detail, flag);
        }

      }
      // console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },

    /** 点击当前行checkbox事件 （select事件） */
    tableTreeSingleSelect(selection, row) {
      if (this.tableRowIdMap.has(row.id) === false) {
        this.tableRowIdMap.set(row.id, true);
      } else if (this.tableRowIdMap.get(row.id) === true) {
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id);
      } else {
        this.tableRowIdMap.set(row.id, true);
      }
      //  console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      //  console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if (row.detail) {
        this.spliteTable(row.detail);
      }
    },

    /**  选择当前行  */
    handleSelectTreeCurrent(row) {
      //  console.info("handleSelectCurrent_row.id:"+row.id);
      if (row.status === 'false') {
        row.status = 'true';
      } else if (row.status === 'true') {
        row.status = 'false';
      }

      if (row.detail != undefined) {
        this.spliteTable(row.detail);
      }

      this.$refs.thermalRoleTreeTable.toggleRowSelection(row);

      if (this.tableRowIdMap.has(row.id) === false) {
        this.tableRowIdMap.set(row.id, true);
      } else if (this.tableRowIdMap.get(row.id) === true) {
        // this.tableRowIdMap.set(row.id,false);
        this.tableRowIdMap.delete(row.id);
      } else {
        this.tableRowIdMap.set(row.id, true);
      }
    },

    /** 权限树初始化 */
    initAccountTableTreeData(data) {
      if (this.permissionType === 'all') {
        this.tableTreeData = [
          {
            caption: '银行用户权限',
            id: '00-0',//特殊标识
            detail: this.initAccTreeData(data.heatconfig.permissionconfig.detail)
          }
        ];
      } else {
        this.tableTreeData = this.initAccTreeData(data.permissionconfig.detail);
      }
      this.tableDataClone = this.tableTreeData;
    },

    /** 当选项发生改变时触发 */
    tableTreeSelectChange(val) {
      // this.multipleSelection = val;
      this.menuNum = this.tableRowIdMap.size;
      //  console.info("change-选择的菜单树行："+JSON.stringify( this.multipleSelection) );
    },

    /** 重新渲染treeTable */
    initLoopCheckTable(rows) {//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {
        // console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
        if (rows[i].status === 'false') {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id);
          this.$refs.thermalRoleTreeTable.toggleRowSelection(rows[i], false)
        } else if (rows[i].status === 'true') {
          this.tableRowIdMap.set(rows[i].id, true);

          this.$refs.thermalRoleTreeTable.toggleRowSelection(rows[i], true)
        }
        if (rows[i].detail) {
          this.initLoopCheckTable(rows[i].detail);
        }
      }

    },

    /** 将菜单权限树种初始化数据种 status为false的移除 */
    initAccTreeData(row) {
      for (let i = 0; i < row.length; i++) {
        if (row[i].detail) {
          this.initAccTreeData(row[i].detail);
        }
        if (row[i].status === 'false') {
          row.splice(i, 1);
          i--;
        }
      }
      return row;
    },

    /** 清空treeTable的复选框 */
    noCheckTreeTable(rows) {//循环检测选中状态
      this.tableRowIdMap = new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.thermalRoleTreeTable.toggleRowSelection(rows[i], false)
        rows[i].status = false;
        if (rows[i].detail) {
          this.noCheckTreeTable(rows[i].detail);
        }
      }
    },

    /***
     * 增加同级或子级
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 1是新增，2是修改，3是删除,4是查询。5是将status修改成true
     *
     */
    checkPermissionId(curId, sourceData, targetData, layer, flag) {
      // console.info("进入checkPermissionId:"+sourceData)
      // console.info("查询选中ID:"+curId);
      var result = false;
      for (var i1 = 0; i1 < sourceData.length; i1++) {
        //  console.info("当前id:"+sourceData[i1].id);
        if (flag === '1') {
          if (sourceData[i1].id === curId) {
            if (layer === '1') {//同级
              sourceData.push(targetData);
              this.tableDataClone = this.tableData;
              //  console.info("add find ok");
              result = true;
              break;
            } else if (layer === '2') {
              if (sourceData[i1].hasOwnProperty("detail")) {
                // console.info("add 存在detail,直接追加:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                this.tableDataClone = this.tableData;
                result = true;
                break;
              } else {
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail;
                //  console.info("add 不存在detail,新创建detail:"+JSON.stringify(sourceData[i1].detail));
                this.tableDataClone = this.tableData;
                result = true;
                break;
              }
            }
            //break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //  console.info("add not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //  console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else if (flag === '2') {
          if (sourceData[i1].id === curId) {
            sourceData[i1].caption = targetData.caption;
            sourceData[i1].vue = targetData.vue;
            sourceData[i1].url = targetData.url;
            sourceData[i1].icon = targetData.icon;
            //  console.info("modify find ok");
            this.tableDataClone = this.tableData;
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //  console.info("modify not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //  console.info("modify not find");
              result = false;
            }
          }
        } else if (flag === '3') {
          if (sourceData[i1].id === curId) {
            sourceData.splice(i1, 1);
            this.tableDataClone = this.tableData;
            //  console.info("del find ok");
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //  console.info("del not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //  console.info("del not find");
              result = false;
            }
          }
        } else if (flag === '4') {
          //  console.info('查询表格内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          //   console.info("query find indexOf:"+sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()));
          if (sourceData[i1].caption.toLowerCase().indexOf(curId.toLowerCase()) > -1) {
            let newdata = [];
            newdata.push(sourceData[i1]);
            this.tableDataClone = newdata;
            //  console.info("query find ok."+ sourceData[i1].caption);
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //  console.info("query not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //  console.info("query not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        } else if (flag === '5') {
          // console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          //  console.info("query find status id:"+sourceData[i1].id);
          if (sourceData[i1].id === curId) {
            sourceData[i1].status = 'true';
            //   console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            //  console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //    console.info("query status not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //    console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

    /** 用于权限树编辑时回显用 目前仅能做到三级回显(已废弃)*/
    checkedTreeTable(roleIds) {
      // console.log("---Tree回显----数组为："+roleIds)
      this.$nextTick(() => {
        this.tableTreeData.forEach(outerItem => {
          // 判断第一层是否有子级
          if (outerItem.detail != '') {
            outerItem.detail.forEach(outerItem1 => {
              //判断第二层是否有子级
              for (let i = 0; i < roleIds.length; i++) {
                if (outerItem1.id === roleIds[i]) {
                  this.$refs.thermalRoleTreeTable.toggleRowSelection(outerItem1, true);
                  //  console.log(outerItem1.caption + "在第二层选中了----：" + outerItem1.caption)
                }
              }
              if (outerItem1.detail != '') {
                outerItem1.detail.forEach(outerItem2 => {
                  //判断treeTable第三层是否有选中的
                  for (let i = 0; i < roleIds.length; i++) {
                    if (outerItem2.id === roleIds[i]) {
                      this.$refs.thermalRoleTreeTable.toggleRowSelection(outerItem2, true);
                      //  console.log(outerItem1.caption + "在第三层选中了----：" + outerItem2.caption)
                    }
                  }
                })
              }
            })
          }
        })
      })
    },

    /** 将用户权限点赋予到tableData */
    initTableDataPermissionStatus(permissionKey, tableData) {
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k, tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        this.initLoopCheckTable(tableData);
      })
    },

