          this.tableBtnRowIdMap.delete(rows[i].id);
        } else {
          this.tableBtnRowIdMap.set(rows[i].id, true, rows[i].caption);
        }

        if (rows[i].detail) {
          this.spliteBtnTable(rows[i].detail, flag);
        }

      }
      // console.info("spliteTable_tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
    },

    /** 点击当前行checkbox事件 （select事件） */
    tableBtnTreeSingleSelect(selection, row) {
      if (this.tableBtnRowIdMap.has(row.id) === false) {
        this.tableBtnRowIdMap.set(row.id, true);
      } else if (this.tableBtnRowIdMap.get(row.id) === true) {
        // this.tableRowIdMap.set(row.id,false);
        this.tableBtnRowIdMap.delete(row.id);
      } else {
        this.tableBtnRowIdMap.set(row.id, true);
      }
      //  console.info("tableRowIdMap:"+JSON.stringify(this.tableRowIdMap));
      //  console.info("tableSingleSelect_row:"+JSON.stringify( row ) );
      if (row.detail) {
        this.spliteBtnTable(row.detail);
      }
    },

    /** 按钮树初始化 */
    initAccountBtnTableTreeData(data) {
      if (this.permissionType === 'all') {
        let buttonDetail = {};
        if (this.customGlobal.userInfoExt.cui_org_type === '0') {
          buttonDetail = data.heatconfig.buttonconfig.detail;
        } else {
          buttonDetail = this.initAccBtnTreeData(data.heatconfig.buttonconfig.detail);
        }
        this.tableBtnTreeData = [
          {
            caption: '银行用户按钮权限',
            vue: '',
            url: '',
            icon: '',
            id: '00-4',//特殊标识
            detail: buttonDetail
          }
        ];
      } else {
        this.tableBtnTreeData = this.initAccBtnTreeData(data.heatconfig.detail);
      }
      this.tableBtnDataClone = this.tableBtnTreeData;
    },

    /** 将菜单权限树初始化数据中 status为false的移除 */
    initAccBtnTreeData(row) {
      for (let i = 0; i < row.length; i++) {
        if (row[i].detail) {
          this.initAccBtnTreeData(row[i].detail);
        }
        if (row[i].status === 'false') {
          row.splice(i, 1);
          i--;
        }
      }
      return row;
    },

    /** 当选项发生改变时触发 */
    tableBtnTreeSelectChange(val) {
      this.btnNum = this.tableBtnRowIdMap.size;
      //  console.log("-选择的所有ID-"+JSON.stringify(this.tableBtnRowIdMap))
    },

    /** 重新渲染treeTable */
    initBtnLoopCheckTable(rows) {//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {
        // console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
        if (rows[i].status === 'false') {
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableBtnRowIdMap.delete(rows[i].id);
          this.$refs.platBtnTreeTable.toggleRowSelection(rows[i], false)
        } else if (rows[i].status === 'true') {
          this.tableBtnRowIdMap.set(rows[i].id, true);

          this.$refs.platBtnTreeTable.toggleRowSelection(rows[i], true)
        }
        if (rows[i].detail) {
          this.initBtnLoopCheckTable(rows[i].detail);
        }
      }

    },

    /** 清空treeTable的复选框 */
    noCheckBtnTreeTable(rows) {//循环检测选中状态
      this.tableBtnRowIdMap = new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.platBtnTreeTable.toggleRowSelection(rows[i], false)
        rows[i].status = false;
        if (rows[i].detail) {
          this.noCheckBtnTreeTable(rows[i].detail);
        }
      }
    },
    /***
     * 回显使用
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 将status修改成true
     *
     */
    checkBtnPermissionId(curId, sourceData, targetData, layer, flag) {
      // console.info("进入checkPermissionId:"+sourceData)
      // console.info("查询选中ID:"+curId);
      var result = false;
      for (var i1 = 0; i1 < sourceData.length; i1++) {
        //  console.info("当前id:"+sourceData[i1].id);
        if (flag === '5') {
          // console.info('查询表格status内容');
          // console.info("query find status id:"+sourceData[i1].id);
          if (sourceData[i1].id === curId) {
            sourceData[i1].status = 'true';
            //   console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            //   console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          } else {
            if (sourceData[i1].hasOwnProperty("detail")) {
              //    console.info("query status not find 递归查询detail")
              result = this.checkPermissionId(curId, sourceData[i1].detail, targetData, layer, flag);
              if (result === true) {
                break;
              }
            } else {
              //    console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

    /** 将用户权限点赋予到tableData */
    initBtnTableDataPermissionStatus(permissionKey, tableData) {
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k, tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        this.initBtnLoopCheckTable(tableData);
      })
    },

    /** 获取用户按钮权限信息 */
    getPlatformUserBtn(username) {
      let _jsondata_ = {
        username: username,
      };
      //  console.info("===查询用户的按钮权限ID数组===传递的参数==="+JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryUserOrgAndPermissionKey,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        if (res.data.status == 'success') {
          // 清空已选择的菜单树 map值，下方需要重新赋值，防止数据重复
          this.tableBtnRowIdMap = new Map();
          this.userQueryBtnIds = {};
          //处理权限ID
          let btnKey = JSON.parse(res.data.btnkey);

          this.userQueryBtnIds = btnKey;
          // console.log("===用户按钮ID查询事件=====查询到的用户按钮权限ID============="+permissionKey);
          let i = 0;
          for (let p1 of Object.keys(btnKey)) {
            if (p1 !== '-1') {
              this.tableBtnRowIdMap.set(p1, true);
              i++
            }
            // console.log("===用户按钮ID查询事件=====查询到的按钮权限ID============="+p1);
          }
          this.btnNum = i;
          // 用户按钮权限回显 先清空 后赋值
          this.noCheckBtnTreeTable(this.tableBtnTreeData)
          this.initBtnTableDataPermissionStatus(this.userQueryBtnIds, this.tableBtnTreeData);
        }
      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '按钮权限查询出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '按钮权限查询查询出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });

    },

    /** 提交用户按钮信息 */
    submitUserBtn() {

      //将存储RoleID的map转换为数组
      let objBtnId = {};
      let i = 0;
      this.tableBtnRowIdMap.forEach(function (value, key) {
        objBtnId[key] = false;
      });
      if (JSON.stringify(objBtnId) === '{}') {
        objBtnId['-1'] = false;
      }
      let _jsondata_ = {
        username: this.loginIdByOrg,
        btnkey: objBtnId
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0,0,0,0.7)'
      });
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postModifyUserAndBtnKey,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        //  console.log("异步调用成功返回:"+JSON.stringify(res));
        this.queryValveCurrentData();
        if (res.data.status == 'success') {
          this.thermalUserBtnVisible = false;
          //提交之后 清空表单及系统公用变量值，防止数据出错
          this.tableBtnRowIdMap = new Map();
          this.noCheckBtnTreeTable(this.tableBtnTreeData);
          loading.close();
          this.$message({
            type: 'success',
            message: '按钮权限保存成功!'
          });
        } else {
          //  console.info("按钮权限保存失败");
          loading.close();
          // this.$alert('登录已超时，请重新登录！', '', {
          //   confirmButtonText: '确定',
          //   callback: action => {
          //     //  console.info('data:'+JSON.stringify(res.data));
          //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
          //       this.$router.replace({path: '/login'});
          //     }
          //   }
          // });
          if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
            this.$router.replace({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        //  console.log("异步调用出错:"+JSON.stringify(error));
      }).catch((res) => {
        loading.close();
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
      });
    },

    /** -----------------组织机构树方法-------------------------- */

    /** 权限树弹出层 */
    showOrgTree(row) {
      // 获取机构树的数据
      this.treeData = JSON.parse(JSON.stringify(this.orgTree.treeData));
      this.treeDataStatus = JSON.parse(JSON.stringify(this.orgTree.treeDataStatus));
      this.initAccOrgTreeData(this.treeData, this.treeDataStatus);

      this.loginIdByOrg = row.cui_login_id;
      this.realNameByOrg = row.cui_realname;
      this.thermalUserOrgVisible = true;
      if (this.checkAllOrgIds.length > 0) {
        this.$nextTick(() => {
          this.noCheckOrgTree(this.checkAllOrgIds);
        })
      }
      this.checkOrgId = [];
      this.checkAllOrgIds = [];
      this.queryUserOrgIds(row.cui_login_id);
    },

    /** 机构树点击checkbox事件 */
    handleCheck(data, node) {
      //  console.log("check事件"+ JSON.stringify(data), node);
      //  console.log("check事件"+ JSON.stringify(data), JSON.stringify( node.checkedKeys) );
      this.checkOrgId = node.checkedKeys;
      if (node.checkedNodes.length > 0) {
        this.selType = node.checkedNodes[node.checkedNodes.length - 1].type;
        // console.log("====机构树checkBox选中事件====查询级别============="+this.selType)
      }
      this.checkAllOrgIds = [];
      this.recursion(this.treeData, false)
      // this.treeCheckedKeys = node.checkedKeys;
      this.checkAllOrgIds = this.checkAllOrgIds.reverse();

      //  console.log("====机构树checkBox选中事件====选中的ID============="+node.checkedKeys)
    },

    /** 查询选中的机构ID的所有父节点 */
    recursion(arr, isChild) {
      let aCheck = [];
      for (let i = 0; i < arr.length; i++) {
        let obj = arr[i];
        aCheck[i] = false;
        if (obj.detail) {
          aCheck[i] = this.recursion(obj.detail, true) ? true : aCheck[i];
          if (aCheck[i]) {
            this.checkAllOrgIds.push(obj['id']);
          }
        }
        for (let j = 0; j < this.checkOrgId.length; j++) {
          if (obj['id'] == this.checkOrgId[j]) {
            aCheck[i] = true;
            if (this.checkAllOrgIds.indexOf(obj['id']) == -1) {
              this.checkAllOrgIds.push(obj['id']);
            }
            break;
          }
        }
      }
      if (isChild) {
        return aCheck.indexOf(true) != -1;
      }
    },

    /** 清空tree的复选框 */
    noCheckOrgTree(rows) {
      this.$refs.thermalOrgTree.setCheckedKeys(rows)
    },

    /** 删除机构树节点 */
    removeTreeNode(node, data) {
      const parent = node.parent
      const children = parent.data.detail || parent.data
      const index = children.findIndex(d => d.id === data.id)
      children.splice(index, 1)
    },

    /** 判断那些节点需要显示 */
    initTreeDataStatus(h, {node, data, store}) {
      if (JSON.stringify(this.treeDataStatus) === "{}") {
        return (
          <span style="font-size:14px;">
               <span>{data.caption}</span>
            </span>
        )
      } else {
        let find = false;
        for (let k of Object.keys(this.treeDataStatus)) {
          if (data.id === k) {
            return (
              <span style="font-size:14px;">
                   <span>{data.caption}</span>
                </span>
            )
            find = true;
            break;
          }
        }
        if (find === false) {
          this.removeTreeNode(node, data);
        }
      }

    },

    /** 将用户的机构权限分类放到数组中1  */
    initAccOrgTreeData(data, data2) {
      // 判断用户类型
      if (this.customGlobal.userInfoExt.cui_type === '0' && this.customGlobal.userInfoExt.cui_org_type === '0') {
        this.selectAdminOrgIds(data);
        return;
      }
      for (let k of Object.keys(data2)) {
        this.selectOrgIds(data, k);
      }
    },

    /** 将用户的机构权限分类放到数组中2  */
    selectOrgIds(row, id) {
      for (let i = 0; i < row.length; i++) {
        if (row[i].id === id) {
          if (row[i].type >= this.customGlobal.userInfoExt.cui_type) {
            switch (row[i].type) {
              case 1:
                this.companyIds.push(row[i].id);
                break
              case 2:
                this.childCompanyIds.push(row[i].id);
                break
              case 3:
                this.instituteIds.push(row[i].id);
                break
              case 4:
                this.stationIds.push(row[i].id);
                break
            }
          }
        }
        if (row[i].detail) {
          this.selectOrgIds(row[i].detail, id);
        }
      }

    },

    /** 将用户的机构权限分类放到数组中 超级管理员使用，拥有全部机构ID  */
    selectAdminOrgIds(row) {
      for (let i = 0; i < row.length; i++) {
        switch (row[i].type) {
          case 1:
            this.companyIds.push(row[i].id);
            break
          case 2:
            this.childCompanyIds.push(row[i].id);
            break
          case 3:
            this.instituteIds.push(row[i].id);
            break
          case 4:
            this.stationIds.push(row[i].id);
            break
        }
        // this.userOrgAngType.push(id);
        if (row[i].detail) {
          this.selectAdminOrgIds(row[i].detail);
        }
      }

    },

    /** 查询出所有机构树信息 */
    initOrgTemplateInfo() {
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryOrganTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({
          jsondata: JSON.stringify({
            config: 'all',
            username: this.customGlobal.userInfoExt.cui_login_id
          })
        })
      );

      p1.then((res) => {
        //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if (res.data.status == 'success') {
          this.treeData = JSON.parse(res.data.organ).detail;
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();

        } else {
          //   console.info("查询失败");
          this.treeData = [JSON.parse(res.data.organ)];
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();


        }

      }, error => {
        //  console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },

    /** 提交银行用户权限标识 */
    submitUserOrg() {
      if (this.checkAllOrgIds.length == 0) {
        this.$alert("机构信息不能为空或您未做变更，无法提交！", '保存银行用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }

      if (this.selType < this.customGlobal.userInfoExt.cui_org_type) {
        this.$alert("您还未取得此机构的权限！", '保存银行用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      let objOrgId = {};
      for (let i = 0; i < this.checkAllOrgIds.length; i++) {
        objOrgId[this.checkAllOrgIds[i]] = true
      }
      //保存用户机构权限
      this.saveUserOrgKeys(this.loginIdByOrg, objOrgId);
      //清空用户的选择
      this.$nextTick(() => {
        this.$refs.thermalOrgTree.setCheckedKeys(this.checkAllOrgIds);
      });
      this.userOrgId = [];
      this.platformUserOrgVisible = false;
      //  console.log("==========用户权限选择============"+this.userOrgId);
    },

    /** 保存用户机构权限 */
    saveUserOrgKeys(loginId, objOrgId) {
      let _jsondata_ = {
        username: loginId,
        orgkey: objOrgId
      };
      const loading = this.$loading({
        lock: true,
        text: '操作中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });
      //  console.info("===保存用户机构权限=====提交参数==="+JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postCreateUserAndOrgKey,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        this.thermalUserOrgVisible = false;
        loading.close();
        if (res.data.status == 'success') {
          this.$message({
            type: 'success',
            message: '银行用户机构信息保存成功!'
          });
        } else {
          this.$alert("请联系系统管理员，并提交错误报告：" + JSON.stringify(error.message), '保存用户机构权限出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }
      }, error => {
        loading.close();
        //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '保存用户机构权限出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
        loading.close();
        //  console.log("异步调用失败返回:"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '保存用户机构权限出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });
    },

  },


}
</script>

<style scoped>
/*<style >*/

.account-card1 /deep/ .el-card__header {
  /*background:#f5f5f5 !important;*/
  background: #f2f3f8 !important;
  /*height: 10px!important;*/
  padding: 10px !important;
}

.company2-card2 /deep/ .el-card__body {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.new-card2 /deep/ .el-card__body {
  padding: 5px 20px 5px 20px !important;

}

.account-formitem /deep/ .el-form-item {
  margin-bottom: 3px !important;
}


.account-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}

.account-table-fixed-right /deep/ .el-table__fixed-right {
  padding-top: 5px !important;
  height: 100% !important;
}

.account-table-fixed /deep/ .el-table__fixed {
  padding-top: 5px !important;
  height: 100% !important;
}

</style>

<style>
